.contacts{
    padding-bottom: 60px;

    .row{
        row-gap: 1.5rem;
    }
    &-title{
        margin-bottom: 50px;
    }
    &__item {
        background-color: #fff;
        padding: 5px;

        &-container {
            padding: 30px 25px 10px 25px;
        }
        &-up{
            
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 15px;
        }


        &-address{
            font-family: "ProximaNovaCond" , sans-serif;
            font-size: 20px;
            font-weight: 700;
            color: #555555;
            span{
                font-weight: 400;
                font-size: 18px;
                color: #808080;
            }
        }
        &-btn{
            font-family: "ProximaNovaCond" , sans-serif;
            font-size: 15px;
            max-width: 160px;
            color: #000;
            padding: 15px 25px;
            display: flex;
            align-items: center;
            gap: 25px;
            background: var(--c-primary);
            line-height: 1;

            i{
                font-size: 24px;
            }
        }
        &-num{
            cursor: pointer;
            font-size: 36px;
            font-weight: 700;
            color: #000;
            &:hover{
                color: inherit;
                opacity: 0.7;
            }
        }
    }
    iframe {
        width: 100%;
        max-height: 340px;
    }
}